*{
  padding:0;
  margin:0;
}
a{
  text-decoration: none;
  color:#0D633D;
}
img{
  width:100%;
}
ul,li,dl,ol{
  list-style: none;
}

/* header */

header{
  width:100%;
  height:5rem;
  display:flex;
  position:fixed;
  top:0;
  left:0;
  background-color: #fff;
  z-index: 1000;
  /* box-shadow: 2px 2px 2px 2px rgba(000, 000, 000, 0.3); */
}
h1{
  width:200px;
  padding-left:1rem;
  padding-top:1rem;
}

nav{
  width:100%;
}
nav>ul{
  display:flex;
  padding-top:2rem;
  font-size: 1rem;
  font-weight: bold;
  color:#0D633D;
}
nav>ul>li{
  padding-left:3rem;
  border-right:1px solid #ccc;
  padding-right:3rem;
}
.loginText{
  width:10rem;
  padding-top:2rem;
  font-size: 1rem;
  font-weight: bold;
  color:#0D633D;
}
.main{
  padding-top:5rem;
}
/* Slide-container */
.slide-container{
  width:100%;
  height:27.56rem;
  overflow: hidden;
  position: relative;
}
.slideBox{
  width:700%;
  height:27.56rem;
  display:flex;
  pointer-events: none;
  position:absolute;
  top:0;
  left:-100%;
}
.active{ transition:.3s ease-in-out;}
.slide-item{
  display: inline-block;
  width:100%;
  height:100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position:0 30%;

}
.slide-item:nth-child(1){
background-image: url(./img/Slide/pic05.jpg);
}
.slide-item:nth-child(2){
background-image: url(./img/Slide/pic01.jpg);
}
.slide-item:nth-child(3){
background-image: url(./img/Slide/pic02.jpg);
}
.slide-item:nth-child(4){
background-image: url(./img/Slide/pic03.jpg);
}
.slide-item:nth-child(5){
background-image: url(./img/Slide/pic04.jpg);
}
.slide-item:nth-child(6){
background-image: url(./img/Slide/pic05.jpg);
}
.slide-item:nth-child(7){
background-image: url(./img/Slide/pic01.jpg);
}
.btnBox{
display: flex;
justify-content:center;
padding-top:.3rem;
}
.arr{
  background-color:#fff;
  border-top:1px;
  border-right:2px;
  border-bottom:2px;
  border-left:1px;
  border-color: #d4d4d4;
  border-style: solid;
  width:20px;
  padding-top:3px;
}
.arr:active{
  border-top:2px;
  border-right:1px;
  border-bottom:1px;
  border-left:2px;
  background-color:#fff;
  border-color: #d4d4d4;
  border-style: solid;
  width:20px;
  padding-top:3px;
}
.page{
  padding:0 .5rem;
}

/*mainText */
.mainText{
  padding-left:10rem;
}
.text01{
  font-size: 2.5rem;
  font-weight: bold;
  display: inline-block;
  line-height:0;
}
.text02{
  font-size: .9rem;
}
/* menu */

.menu{
  margin-top:10rem;
  width:100%;
  height:120px;
  display: flex;
}
.menu>span{
  display: inline-block;
  width:3%;
  height:200px;
  background-color:#0D633D;
}
.menu>ul{
  width:100%;
  height:100%;
  display: flex;
  justify-content: space-evenly;
}
.menu>ul>li{
  width:200px;

}
/* evnet */

.event{
  width:80%;
  margin:12rem auto;
}

.more{
  font-size: 0.8rem;
  text-align: right;
}

/* footer */
.footer{
  width:100%;
  height:50px;
  border-top:2px solid #0D633D;
  text-align: center;
  color:#0D633D;
  line-height: 50px;
  margin-top:3rem;
  margin: 3rem auto 0;
}

/* login */

.login{
  width:100%;
  display: flex;
  padding-top:6rem;
}
.loginImg{
width:50%;
border-right:1px solid #ccc;
}
.loginImg>dt{
  width:300px;
  height:300px;
  background: no-repeat 0 0 url('./img/Slide/pic02.jpg');
  background-size: cover;
  margin: 0 auto;
}
.loginImg>dd{
  width:150px;
  margin: 1rem auto;
}

/* 메인 로그인 스타일 */
.mainLogin{
  width:50%;
  text-align: center;
}
input[name='login']{
  width:15rem;
  height:2rem;
  border-radius: 15px;
  border:1px solid #9b9b9b;
  padding-left:1rem;
  box-sizing: border-box;
}
.id{
  padding-bottom:.5rem;
}

.loginBtn>button{
  width:15rem;
  height:2rem;
  border-radius: 15px;
  background-color: #0D633D;
  border:none;
  color:#fff;
  font-size: 1.1rem;
  margin-top:1rem;
  margin-bottom:1rem;
}
.join{
  width:50%;
  margin:0 auto ;
}
.join>p{
  width:15rem;
  height:2rem;
  border:1px solid #9b9b9b;
  border-radius: 15px;
  text-align: center;
  vertical-align: middle;
  line-height: 2rem;
  margin:0 auto;
  margin-bottom:.5rem;
  font-size: .9rem;
  box-sizing: border-box;
  padding-left:1rem;
  font-weight: bold;
}
.text03{
  font-size: .7rem;

}
.kakao{
  background-color: #fef615;

  color:#000;
}
.naver{
  background-color: #23c649;
  color:#fff;
}
.google{
  background-color: #e4e4e4;
  color:#616161;
}
.kakao{
  vertical-align: middle;
}
.icon{
  position: relative;
}
.icon>img{
  width:20px;
  vertical-align: middle;
  position:absolute;
  top:15%;
  left:10%;
}

/* Brand */
.Brand{
  width:100%;
  display: flex;
  padding-top:6rem;
}
.BrandImg{
width:50%;
border-right:1px solid #ccc;
}
.BrandImg>dt{
  width:300px;
  height:300px;
  background: no-repeat 0 0 url('./img/Slide/pic03.jpg');
  background-size: cover;
  margin: 0 auto;
}
.BrandImg>dd{
  width:150px;
  margin: 1rem auto;
}
.brandText{
  width:50%;
}
.brandTextBox>dt{
width:20%;
display: flex;
margin: 0 auto;
}
.brandTextBox>dd{
  text-align: center;
  padding-top:3rem;
  font-size: .9rem;
}
dd>strong{
  font-size: 1.3rem;
  font-weight: bold;
}
.text04{
  width:50%;
  padding-left:2rem;
  box-sizing: border-box;
  color: #fff;
  background-color: #0D633D;
}
.text04Box{
  padding-top:1rem;
}
.text04>dt{
  font-weight: bold;
  padding-left:1rem;
}
.text04>dd{
  font-size: .8rem;
  padding:.2rem 0;
}
.mainMenu{
  width:50%;
  margin:0 auto;
  box-sizing: border-box;
  padding-top:3rem;
  padding-bottom:3rem;
}
.menu01{
  text-align: center;
  font-weight: bold;
  margin:2rem auto 0;
}
.menu01>dt{
  width:15rem;
  padding-bottom:2rem;
  margin:0 auto;
}
.pic01{
  display: table-cell;
  padding-bottom:2rem;
  box-sizing: border-box;
}
.pic01 .text05{
  font-size: .8rem;
}
.pic01 .text06{
  font-size: .6rem;
}
.menu01>dd{
  display: table;
}
.menu01>dd>ul{
  display: table-row-group;
}

/* event style */
.render_event{
  width:100%;
  height:12rem;
  padding:2rem 3rem;
  border:1px solid #000;
  border-radius: 15px;
  box-sizing: border-box;
  display:flex;
  justify-content: space-between;
  line-height: 8rem;
  margin-bottom:2rem;
}
.render_event:hover{
  background-color: #0D633D;
  color:#fff;
  cursor: pointer;
}
.event_img{
  width:6rem;
  height:8.5rem;
  border:1px solid #000;
  vertical-align:middle;
  background-color: #fff;
}
.event_title{
  font-weight: bold;

}